import uniPopup from '@/components/uni-popup/uni-popup.vue'
import xselect from '../../wxcomponents/xselect/xselect.vue'
import request from '../../components/pocky-request/index.js'

export default {
	onLoad(sid) {
		uni.getStorage({
			key:'phonetype',
			success:res=>{
				this.phonetype = res.data
				this.setStorageBrand()
			}
		})
		
		
		
		uni.getStorage({
			key: 'sid',
			success: res=> {
				let s = res.data
				this.quickId = s
			}
		})
		
		this.requestUrl()
		uni.request({
			url: 'https://www.finetwm.com/xmRepair/phoneBrand/getBrandDetail',
			method: 'POST',
			data: {
				id:1
			},
			success: res => {
				this.phoneType = res.data.data
			}
		})
		
		if(sid.id){
			this.quickId = sid.id
		}else if(sid.model){
			let smodel = sid.model
			uni.setStorage({
				key:'model',
				data:smodel,
				success:res=>{
					uni.request({
						url:'https://www.finetwm.com/xmRepair/phoneBrand/getBrandColour',
						data:{
							name: smodel
						},
						method:'POST',
						success:res=>{
							this.popupContent = res.data.data
							this.$refs.popup.open()
						}
					})
				}
			})
			console.log('本机维修')
		}else{
			return
		}
		
	},
	data() {
		return {
			phonetype:'',
			quickId:'',
			valueId:1,
			colorId:null,
			selectedId: 0,
			popupContent: [{
					name: "银色",
					value: "#E7E3E2"
				},{
					name: "金色",
					value: "#DAC8B4"
				},{
					name: "深空灰",
					value: "#2A2526"
				}],
			isSelected: false,
			phone: '手机',
			pad: '平板',
			slideList: [],
			phoneType: []
		}
	},
	methods: {
		bindMyPhoneColor(){
			if(this.phonetype){
				uni.request({
					url:'https://www.finetwm.com/xmRepair/phoneBrand/getBrandColour',
					data:{
						name: this.phonetype
					},
					method:'POST',
					success:res=>{
						this.popupContent = res.data.data
						this.$refs.popup.open()
					}
				})
			}
		},
		setStorageSync(info){
			uni.setStorage({
				key: 'brand',
				data: info
			})
		},
		setStorageBrand(){
				let s = this.phonetype.slice(0,2)
				if(s == 'iP') {
					this.setStorageSync('苹果')
				} else if(s == 'Ma' || s == 'P3' || s == 'P2' || s == 'P1' || s == 'P9' 
				|| s == 'P8' || s == 'P7' || s == 'No' || s == '荣耀' || s == '畅玩' || s == '畅享'){
					this.setStorageSync('华为')
				} else if(s == '红米' || s == '小米'){
					this.setStorageSync('小米')
				} else if(s == 'iQ' || s == 'vi'){
					this.setStorageSync('VIVO')
				} else if(s == 'OP'){
					this.setStorageSync('OPPO')
				} else if(s == '魅族' || s == '魅蓝'){
					this.setStorageSync('魅族')
				} else if(s == 'No' || s == 'S1' || s == 'S9' || s == 'S8' || s == 'S7' 
				|| s == 'S6' || s == 'S5' || s == 'S4' || s == 'C9' || s == 'C8' || s == 'C7' || s == 'A8'
				|| s == 'A7' || s == 'A6' || s == 'A4' || s == 'A9' || s == 'W2' ){
					this.setStorageSync('三星')
				}
				
			
					
			
		},
		onNext() {
			if(this.colorId !== null){
				uni.navigateTo({
					url: '../faults/faults?id=' + this.quickId
				})
			}
		},
		openPopup(e) {
			if(e){
				let modelSelected = this.phoneType[e].model
				uni.setStorage({
					key:'model',
					data: modelSelected
				})
				this.$refs.popup.open()
				//获取颜色色值
				uni.request({
					url: 'https://www.finetwm.com/xmRepair/phoneBrand/getBrandColour',
					method: 'POST',
					data: {
						name: modelSelected
					},
					success: res => {
						this.popupContent = res.data.data
						// uni.setStorage({
						// 	key:'color',
						// 	data:svalue
						// })
					}
				})
			} else {
				this.$refs.popup.open()
			}
		},
		closePopup() {
			this.$refs.popup.close()
			this.colorId = null
		},
		onSelectedId(e) {
			this.selectedId = e.currentTarget.id
			this.valueId = parseInt(e.currentTarget.id) + 1
			let svalueId = e.currentTarget.id
			let svalue = this.slideList[svalueId].brand
			uni.setStorage({
				key:'brand',
				data:svalue
			})
			uni.request({
				url: 'https://www.finetwm.com/xmRepair/phoneBrand/getBrandDetail',
				method: 'POST',
				data: {
					id:this.valueId
				},
				success: res => {
					this.phoneType = res.data.data
				}
			})
		},
		onSelectedColor(e){
			this.colorId = e.currentTarget.id
			// console.log(this.colorId)
			let selectedColorName = this.popupContent[this.colorId].name
			let selectedColorValue = this.popupContent[this.colorId].value
			uni.setStorage({
				key:'colorName',
				data:selectedColorName
			})
			uni.setStorage({
				key:'colorValue',
				data:selectedColorValue
			})
		},
		
		requestUrl(){
			const instance = new request()
			const requestModel = instance.get({
				url:'/phoneBrand/getBrand',
				contentType: 'json',
			}).then(res=>{
				this.slideList = res.data.data
				console.log(res)
			}).catch(err=>{
				console.log(err)
			})
		}

	},
	components: {
		uniPopup,
		xselect,
	}
}
